import { PIXIHooks, StatsJSAdapter } from "gstats/dist/main";
import { Application } from "pixi.js";

window.Stats = require("stats.js/build/stats.min.js");

export class FPS {
    constructor(app: Application) {
        // We're silencing the error message (not the actual error) from the PIXIHooks constructor
        // so it does not flood the analytics when WebGL is not supported

        // eslint-disable-next-line no-console,@typescript-eslint/unbound-method
        const original = console.error;
        // eslint-disable-next-line no-console,@typescript-eslint/unbound-method
        console.error = () => undefined;
        const pixiHooks = new PIXIHooks(app);
        // eslint-disable-next-line no-console,@typescript-eslint/unbound-method
        console.error = original;

        const stat = new StatsJSAdapter(pixiHooks);
        const stats = stat.stats as { dom?: HTMLElement; domElement?: HTMLElement };
        const element = (stats.dom ?? stats.domElement) as HTMLElement;
        element.style.left = "200px";
        makeDraggable(element);
        document.body.appendChild(element);
        app.ticker.add(stat.update.bind(stat));
    }
}

function makeDraggable(element: HTMLElement) {
    element.addEventListener("mousedown", (event) => {
        const shiftX = event.clientX - element.getBoundingClientRect().left;
        const shiftY = event.clientY - element.getBoundingClientRect().top;
        let dragged = false;

        function moveAt(pageX: number, pageY: number) {
            element.style.left = `${pageX - shiftX}px`;
            element.style.top = `${pageY - shiftY}px`;
        }

        moveAt(event.pageX, event.pageY);

        function onMouseMove(event: MouseEvent) {
            moveAt(event.pageX, event.pageY);
            dragged = true;
        }

        // move the element on mousemove
        document.addEventListener("mousemove", onMouseMove);

        // drop the element, remove unneeded handlers
        const mouseUpHandler = (e: MouseEvent) => {
            document.removeEventListener("mousemove", onMouseMove);
            document.removeEventListener("mouseup", mouseUpHandler);
            if (dragged) e.stopPropagation();
        };

        element.addEventListener("mouseup", mouseUpHandler);
    });
}
