import { sendEvent } from "../..";
import { gameSettings, stateManager } from "../../core/BaseGame";
import { AdType, getStorageData, setStorageData, shouldPlayFirstAds, showADS } from "../../core/natives/SocialManager";
import { sounds } from "../../core/SoundManager";
import { BaseState } from "../../core/states/BaseState";
import { Button } from "../../core/ui/Button";
import { calculateStars } from "../Game";
import { GameViewState } from "./GameViewState";
import { Logo } from "./menu/Logo";
import { MapItem } from "./menu/MapItem";

export class MenuState extends BaseState {
    private logo: Logo;
    private activeLevelItem: MapItem;
    private activeLevelId: number;
    private left: Button;
    private right: Button;
    private canPlay: boolean;
    private playBtn: Button;
    private muteBtn: Button;
    private unmuteBtn: Button;

    public create(): void {
        this.logo = new Logo();
        this.addChild(this.logo);
        this.playBtn = new Button("start_button.png", "start_button.png");
        this.playBtn.position.y = gameSettings.originalHeight / 3 - 30;
        this.addChild(this.playBtn);
        this.playBtn.on("pointerup", () => {
            this.onPlayClicked();
        });

        this.muteBtn = new Button("sound1.png", "sound1.png");
        this.muteBtn.position.set(gameSettings.originalWidth / 2 - 50, -gameSettings.originalHeight / 2 + 70);
        this.addChild(this.muteBtn);
        this.muteBtn.on("pointerup", () => {
            sendEvent("Menu", "Mute", "click");
            this.mute = true;
        });

        this.unmuteBtn = new Button("sound2.png", "sound2.png");
        this.unmuteBtn.position.set(gameSettings.originalWidth / 2 - 50, -gameSettings.originalHeight / 2 + 70);
        this.addChild(this.unmuteBtn);
        this.unmuteBtn.on("pointerup", () => {
            sendEvent("Menu", "Unmute", "click");
            this.mute = false;
        });
        getStorageData("emoji_mute", "0").then((m)=> {
            this.mute = m === "1";
        });

        this.createMapItems();
    }
    private set mute(m: boolean) {
        this.muteBtn.visible = !m;
        this.unmuteBtn.visible = m;
        setStorageData("emoji_mute", m ? "1" : "0");
        sounds.muteMusic = m;
        sounds.muteSounds = m;
    }
    public start(...params: any): void {
        sendEvent("Menu", "View", "Start");
        this.logo.start();
        this.setActiveLevel();
    }
    public async close() {
        sendEvent("Menu", "View", "Close");
        this.logo.stop();
    }
    private async setActiveLevel() {
        const {levels} = gameSettings;
        let i = 0;
        let stars = -1;
        for (const data of levels) {
            const scoreS = await getStorageData(`${data.key}`,"");
            stars = -1;
            if (scoreS !== "") {
                data.score = parseInt(scoreS);
                stars = calculateStars(data.score, data.starsConditions);
            }
            if (stars === 0 || stars === -1) {
                break;
            } else {
                i++;
            }
        }
        this.activeLevelId = i;
        this.canPlay = i === 0 || stars !== -1;
        this.activeLevelItem.updateData(this.activeLevelId, stars);
        this.updateButtons();
    }
    private updateButtons() {
        this.playBtn.disabled = !this.canPlay;
        this.left.visible = this.activeLevelId > 0;
        this.right.visible = this.activeLevelId < gameSettings.levels.length-1;
    }
    private createMapItems() {
        this.activeLevelItem = new MapItem();
        this.activeLevelItem.position.set(0, gameSettings.originalHeight / 3 - 230);
        this.addChild(this.activeLevelItem);

        this.left = new Button("back_button.png", "back_button.png");
        this.left.position.set(-240, this.activeLevelItem.y);
        this.addChild(this.left);

        this.right = new Button("next_button.png", "next_button.png");
        this.right.position.set(250, this.activeLevelItem.y+5);
        this.addChild(this.right);

        this.left.scale.set(0.5, 0.5);
        this.right.scale.set(0.5, 0.5);

        this.left.on("pointerup", () => {
            sendEvent("Menu", "Prev", "Click");
            this.activeLevelId--;
            this.updateActive();
        });
        this.right.on("pointerup", () => {
            sendEvent("Menu", "Next", "Click");
            this.activeLevelId++;
            this.updateActive();
        });
    }
    private async updateActive() {
        const data = gameSettings.levels[this.activeLevelId];
        const scoreS = await getStorageData(`${data.key}`,"");
        let stars = -1;
        if (scoreS !== "") {
            data.score = parseInt(scoreS);
            stars = calculateStars(data.score, data.starsConditions);
        }
        this.canPlay = this.activeLevelId === 0 || stars !== -1;
        this.activeLevelItem.updateData(this.activeLevelId, stars);
        this.updateButtons();
    }
    private async onPlayClicked() {
        if (!this.canPlay) return;
        if (shouldPlayFirstAds()) {
            await showADS(AdType.Interstitial);
        }
        sendEvent("Menu", "Play", "Click", this.activeLevelId + 1);
        stateManager.start(GameViewState, this.activeLevelId);
        //stateManager.start(MapState);
    }
}