import { Rectangle, Sprite, Texture } from "pixi.js";
import { METER_SIZE } from "../../../../core/BaseGame";
import { BaseBonus } from "./BaseBonus";
import gsap, { Expo, Linear } from "gsap";
import { BonusIntroLetter } from "./BonusIntroLetter";

export class BoomLettersBonus extends BaseBonus {
    private static allTexts: Map<0|1|2|3, string[]>;
    private points: [number, number][] = [];
    public get activeBound() {
        if (this.points.length > 0) {
            const arr: Rectangle[] = [];
            for (let i = 0; i < this.points.length; i++) {
                const point = this.points[i];
                arr.push(new Rectangle(point[0] - 1, point[1] - 1, 2, 2))
            }
            this.points = [];
            return arr;
        }
        return undefined;
    }
    public clean(): void {
        this.points = [];
        super.clean();
    }  
    protected getLetters(level: 0|1|2|3): string[] {
        return BoomLettersBonus.getAllByAmount(level);
    }
    protected animate(items: BonusIntroLetter[]): Promise<void> {
        this.points = [];
        return new Promise<void>((resolve)=> {
            if (this.getPoint) {
                for (let i = 0; i < items.length; i++) {
                    const currentPoint = this.getPoint();
                    if (!currentPoint) {
                        continue;
                    } else {
                        gsap.to(items[i].scale, {x:0, y:0, duration: 0.2});
                        gsap.to(items[i], {x: currentPoint[0] * METER_SIZE, y: currentPoint[1] * METER_SIZE, duration: 0.2, onComplete: ()=> {
                            this.points.push(currentPoint);
                        }});
                    }
                }
                gsap.delayedCall(0.25, ()=> {
                    resolve();
                });
            } else {
                resolve();
            }
        });
    }
    private static getAllByAmount(level: 0|1|2|3): string[] {
        if (level === 0) return [];
        if (!BoomLettersBonus.allTexts) {
            const texts = [
                "BANG!",
                "BONUS", 
                "SMASH",
                "KABOOM",
                "CRASH",
                "CLANK",
                "THUMP",
                "WHAM!",
                "SUPER BOOM",
                "SUPER BONUS",
                "LET'S JAZZ",
                "YOU ARE JUST GREAT",
                "YOU ARE GREAT",
                "SUPER PUPER BOOM",
                "WISH YOU LUCK, DUDE!",
                "THAT WAS DOOM!",
                "OH MY GOD!"
            ];
            BoomLettersBonus.allTexts = new Map<0|1|2|3, string[]>();
            texts.forEach((t)=> {
                const i = (t.length >= 17) ? 3 : (t.length >= 10 ? 2 : 1);
                let arr: string[] = [];
                if (BoomLettersBonus.allTexts.get(i)) {
                    arr = BoomLettersBonus.allTexts.get(i)!;
                }
                arr.push(t);
                BoomLettersBonus.allTexts.set(i, arr);
            });
            console.log(BoomLettersBonus.allTexts);
        }
        const arr = BoomLettersBonus.allTexts.get(level)!;
        return arr[Math.floor(Math.random()*arr.length)].split("");
    }
}